html {
  box-sizing: border-box;
  background-color: #1b1b1b;
  height: -webkit-fill-available;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

/*
* {
  box-sizing: border-box;
  background-color: #1b1b1b;
}
*/

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
    margin: 0;
    height: -webkit-fill-available;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#layout {
  display: flex;
  flex-flow: column;
  height: 100%;
  height: 100vh;
}

#navigation {
  background-color: #333;
  color: whitesmoke;
  font-variant: small-caps;

  justify-content: space-between;
  
  align-items: center;
  text-align: center;
  
  height: 7.5%;
  top: 0px;

  gap: 2rem;
  padding: 0 15rem;

  position: -webkit-sticky fixed;
  position: sticky;

  z-index: 3;
}

#navigation a {
  color: inherit;
  padding: 0 1rem;
}

#nav-links {
  display: flex;
  flex-flow: row;
  list-style: none;
  
  text-align: inherit;
  position: absolute;
  
  margin: 50px 705px 0px 0px;
  bottom: 0px;
  right: 355px;
  left: 10rem;
  padding: 0;
  gap: 35px;
}

.nav-link > a {
  position: relative;
  right: 10%;
  bottom: 15%;
  text-decoration: none;
}

#navigation li {
  background-color: #1b1b1b;
  padding: 3.75px;
  font-weight: 500;
  letter-spacing: 1.75px;

  transition: ease-in-out 0.15s;
}

#navigation li:hover {
  color: goldenrod;
  border-radius: 50px;

  letter-spacing: 4px;
  font-weight: bold;

  padding: -10px 1px 0px 1px;

  transition: ease-in-out 0.45s;
}

#navigation li:active{
  text-decoration:line-through;
}

#nav-badge {
  display: none;
}

#socials {
  width: 500px;
  height: 50px;
  
  position: absolute;
  right: 1.5rem;
  top: 20px;
}

#socials > a > img {
  height: 35px;
  padding: 0px 2px 0px 1px;
  border: #00000000 solid 2px;
  border-radius: 0px;
  transition: 0.5s ease-out;
}

#socials > a > img:hover {
  border: goldenrod solid 2px;
  border-radius: 50px;
  transition: 0.5s ease-in;
}

#nav-badge > img:hover {
  background-color: #1b1b1b;
  border: #1b1b1b solid 2px;
  border-radius: 25px;
  border-width: 5px;

  content: url("./media/banner-open.png");

  transition: ease-in-out 0.35s;
}

#nav-title {
  position: absolute;

  color: whitesmoke;
  font-size: 1.5rem;
  font-weight: 785;
  
  text-align: left;
  word-spacing: 15px;
  letter-spacing: 10px;

  margin-top: 1rem;
  left: 285px;
  bottom: 35px;

  transition: 0.45s;
}

#nav-title > a {
  text-decoration: none;
}

#nav-title:hover {
  color: goldenrod;
  background-color: #1b1b1b;

  font-weight: 850;
  word-spacing: 20px;
  letter-spacing: 16px;

  border-radius: 50px;
}

#nav-title:active{
  text-decoration:line-through;
}

#content {
  background-color: #1b1b1b;
  background-image: url('./media/bg.png');
  background-position: center 45px;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: contain;

  color: whitesmoke;
  align-items: center;
  text-align: center;
  
  display: flex;
  flex-flow: column;

  height: 90%;
  width: 100%;
  flex-grow: 1;
  
  z-index: 1;
}

#home {
  flex: 1;
}

.intro {
  font-size: 0.85rem;
  font-weight: 650;
  letter-spacing: 5px;
  color: goldenrod;
  background-color: #333;

  text-align: center;
  margin: 0 50px 0;
  padding: 0;

  height: 11.5px;
  position: static;
}

.text-highlight {
  color: goldenrod;
  font-weight: 550;
}

p.panel {
  border: 1px dotted rgb(105, 105, 105);
  background-color: rgba(17, 17, 17, 0.384);
  
  margin: 75px 50px 0px;
  padding: 15px;
  
  width: 600px;
  
  font-size: 1.25rem ;
  
  position: relative;
  left: 0%;
}

#education {
  margin-top: 50px;
}
#education > .panel {
  margin-top: 0;
  margin-bottom: 15px;
  padding: 2px;
}

#experience {
  margin-top: 50px;
  margin-bottom: 50px;
}
#experience > .panel {
  margin-top: 0px;
}

#skills-list {
  border: whitesmoke solid 1px;
  border-radius: 50px;

  display: grid;
  grid-template-columns: repeat(2,1fr);

  position: fixed;
  padding-top: 10px;
  padding-bottom: 20px;
  
  left: 67%;
  top: 23%;

  width: 600px;
}

#skills-list h3 {
  text-align: center;
  margin: 0px 0px;

  letter-spacing: 2px;
  color: goldenrod;
  font-weight: 700;
  padding: 10px 0px 10px 10px;
}

#skills-list li {
  text-align: left;
  list-style-position: inside;
  list-style-type:square;

  border: whitesmoke solid 1px;
  color: goldenrod;
  font-weight: 550;
  justify-content: center;
  
  padding: 10px;
  margin: 5px 45px 10px 0px;
  width: 200px;
}
#art > .panel > a:hover {
  color: goldenrod;
  font-size: 22px;
  transition-duration: 0.25s;
}
#art > .panel > a {
  color: whitesmoke;
  transition: ease-in-out;
  transition-duration: 0.85s;
  font-size: 18px;
}

#artwork-panel {
  background-color: #33333371;
  width: 55%;

  left: 25%;

  position: absolute;
  align-items: center;
  align-content: center;
  align-self: center;
  
  margin: 100px auto 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
}

#art-window {
  background-color: #333;
  position: fixed;
  
  height: 85vh;
  width: 100vh;

  bottom: 5vh;
  left: 60vh;

  z-index: 2;
}

#art-window:empty {
  height: 0px;
  display: none;
}

#art-window > img {
  height: 95%;
  z-index: 1;

  position: absolute;
  align-self: center;
  top: 2.5%;
  left: 5%;
}
#art-window > button {
  background-color: whitesmoke;
  border: #333 5px;
  border-radius: 6.5px;
  padding: 5px;
  
  font-size: 22px;
  color: black;
  text-shadow: #1b1b1b;
  font-weight: 600;
  text-align: center;

  position: absolute;
  top: 5%;
  left: 88.5%;

  transition: ease-in-out 0.65s;
}

#art-window > button:hover {
  background-color: #1b1b1b;
  box-shadow: 0 12px 16px 0 whitesmoke;
  
  color: whitesmoke;
  text-shadow: snow ;
}

.thumbnail {
  border: 3.5px solid whitesmoke;
  
  padding: 5px;
  margin: 5px 0px 0px 0px;

  width: 200px;
  height: 235px;

  transition: ease-in-out;
  transition-duration: 0.556s;
}

.thumbnail:hover {
  border: 3.5px solid goldenrod;

  padding: 10px;
  width: 250px;
  height: 230px;

  transition-duration: 0.25s;
  z-index: 2;

  position: relative;
}

.thumbnail > img {
  border: 3.5px solid goldenrod;
  width: 20%;
  filter: grayscale();
  transition: ease-in-out;
  transition-duration: .75s;
}
.thumbnail > img:hover {
  border: 3.5px solid goldenrod;
  width: 100px;
  filter: none;
  transition: ease-in-out;
  transition-duration: .75s;
}

#blog > .panel {
  right: 0rem;
}

#blog > ul {
  position: relative;
  
  align-self: center;
  list-style-type: none;

  margin-left: 60px;
}

#blog > ul > .blog-post {
  border: goldenrod 2px solid;

  padding: 0px 20px 20px 20px;
  margin-top: 30px;
  margin-bottom: 20px;

  width: 500px;
}

#blog > ul > .blog-post > h3 {
  font-weight: 200;
  margin-bottom: -5px;
  letter-spacing: 3.5px;
  word-spacing: 5px;
}

#blog > ul > .blog-post > a {
  color: whitesmoke;
  background-color: #000000b0;

  padding: 5px 10px 5px 10px;
}

#code > .panel {
  right: 0rem;
}

#code > ul {
  position: relative;

  align-self: center;
  list-style-type: none;

  margin-left: 60px;
}

#code > ul > li {
  padding: 0px 20px 20px 20px;
  margin-top: 30px;
  margin-bottom: 20px;

  width: 500px;

  border: goldenrod 2px solid;
}

#code > ul > li > h3 {
  font-weight: 200;
  margin-bottom: -5px;
  letter-spacing: 3.5px;
  word-spacing: 5px;
}

#code > ul > li > p {
  text-align: left;
}

#code > ul > li > a {
  color: whitesmoke;
  background-color: #000000b0;

  padding: 5px 10px 5px 10px;
}

#code > ul > li > span > a {
  color: rgb(212, 229, 243);
  background-color: #000000b0;

  padding: 0px 10px 9px 10px;
  margin-top: 10px;
}

#games-list {
  list-style-type: none;
  margin-top: 50px;
}

#games-list > li {
  background-color: #1c1c1cb0;
  border: goldenrod solid 2px;

  margin-top: 10px;
  margin-bottom: 15px;
  padding-bottom: 10px;

  margin: 25px 0px 10px 50px;

  width: 500px;
  height: 50%;
}

#games-list > li > h3 {
  margin-bottom: -5px;
  letter-spacing: 3.5px;
  word-spacing: 5px;
  font-weight: 200;
}

#games-list > li > p {
  padding: 0px 10px 0px 10px;
  text-align: center;
}

#games-list > li > a {
  color: whitesmoke;
  background-color: #000000b0;

  top: 100px;
  bottom: 100px;
  margin: 100px 0px 100px 0px;
  padding: 5px 10px 5px 10px;
  /*
  */
}

#error-page {
  background-color: whitesmoke;
  color: #333;
  align-self: center;
  align-content: center;
  text-align: center;
}

/*
    Below is for resolutions lower than 1220px in width
*/
@media (max-width: 1220px) {

  #nav-badge {
    display: inherit;
  }
  
  #nav-badge > img {
    background-color: #333;
    border: #1b1b1b solid 2px;

    position: absolute;
    
    color: whitesmoke;
    
    height: 50px;
    width: 50px;
    
    right: 13.5rem;
    top: 20px;

    transition: ease-in-out 0.35s;
  }

  #nav-badge > img.open-menu {
    right: 0.5rem;
  }

  #nav-title {
    font-size: 1.2rem;
    font-weight: 785;
    
    text-align: left;
    word-spacing: 15px;
    letter-spacing: 10px;

    margin-top: 1rem;
    left: 5px;
    bottom: 5px;
  }

  #navigation #nav-links {
    display: none;
    flex-direction: column;

    position: absolute fixed;

    margin: 0px 0px 0px 0px;
    top: 5rem;
    left: 0rem;
    right: 35rem;
    width: 100%;

    background-color: #333;
  }

  #navigation #nav-links.show {
    display: inherit;
  }

  #navigation #socials {
    display: none;
    flex-direction: column;

    position: absolute fixed;
    margin: 0px 0px 0px 0px;
    left: 0rem;
    gap: 1rem;

    background-color: #333;
  }
  #navigation #socials.show {
    display: inherit;
  }

  #skills-list {
    position: inherit;
    align-self: center;

    display: grid;
    grid-template-columns: repeat(2,1fr);

    left: 6.25rem;
  }
/*
    Below is for resolutions lower than 650px in width
*/
  @media (max-width: 650px) {
    html {
      font-size: x-small;
    }
    
    #nav-badge > img {
      right: 0.5rem;
    }

    .intro {
      letter-spacing: .25rem;
      margin: 0 0rem 0;
    }

    p.panel {
      margin: 1rem 15rem 1rem 0rem;
      padding-left: 25rem;
      padding-right: 10rem;
    }
    
    #navigation #socials {
      background-color: #1b1b1b;
      flex-direction: column-reverse;
      top: 15.65rem;
    }

    #skills-list {
      position: inherit;
      align-self: center;
      align-content: center;

      width: 50%;
  
      display: grid;
      grid-template-columns: none;

      margin: auto
    }

    #skills-list li {
      text-align: center;

      position: relative;
      margin-left: 5rem;
    }

    #blog > ul {
      margin: auto;
    }

    #blog > ul > .blog-post {
      margin: 1rem auto auto auto;
      width: 50%;
    }

    #code > ul {
      margin: auto;
    }

    #code > ul > li {
      margin: 1rem auto auto auto;
      width: 50%;
    }

    #games-list {
      margin: auto;
    }

    #games-list > li {
      margin: 1rem auto auto auto;
      width: 50%;
      height: 10%;
    }
  }
}